<template>
  <el-row class="exams-container">
    <ex-header></ex-header>
    <el-carousel trigger="click" height="50vh" :autoplay="false">
      <el-carousel-item
        v-for="item in carousels"
        :key="item.id"
        :style="{ backgroundImage: `url(${item.background})` }"
      >
        <h1>{{ item.title }}</h1>
      </el-carousel-item>
    </el-carousel>
    <main class="exams-main el-container">
      <el-row>
        <el-col :md="8" v-for="item in exams" :key="item.id">
          <el-card>
            <div slot="header">
              <span>{{ item.title }}</span>
              <el-button
                type="text"
                @click="() => $router.push({ path: '/answer/' + item.id })"
              >
                开始
              </el-button>
            </div>
            <div class="note" :title="item.notes">
              {{
                item.notes === "" || item.notes === null ? "无备注" : item.notes
              }}
            </div>
            <div class="time">答题时间：{{ item.answer_time }} 分钟</div>
            <div class="time">
              开始时间：{{ new Date(item.start_time * 1000).toLocaleString() }}
            </div>
            <div class="time">
              结束时间：{{ new Date(item.end_time * 1000).toLocaleString() }}
            </div>
          </el-card>
        </el-col>
      </el-row>
    </main>
    <ex-footer></ex-footer>
  </el-row>
</template>

<script>
import { mapActions } from "vuex";
import Header from "../../components/front/Header";
import Footer from "../../components/front/Footer.vue";
export default {
  name: "exams",
  components: {
    "ex-header": Header,
    "ex-footer": Footer
  },
  data() {
    return {
      carousels: [
        {
          title: "欢迎",
          background: "/index-1.jpg"
        },
        {
          title: "欢迎",
          background: "/index-1.jpg"
        },
        {
          title: "欢迎",
          background: "/index-1.jpg"
        },
        {
          title: "欢迎",
          background: "/index-1.jpg"
        }
      ],
      exams: []
    };
  },
  methods: {
    ...mapActions("exam", ["getExams"])
  },
  mounted() {
    this.getExams()
      .then(res => {
        this.$set(this, "exams", res.data.data);
      })
      .catch(err => {
        this.$notify.error({
          title: "获取失败",
          message: err.data.error
        });
      });
  }
};
</script>

